<template>
	<div>
    <el-button type="primary" size="small" @click="$router.push({ name: 'versionAdd' })"></el-button>
		<el-table :data="pageInfo.tableList" highlight-current-row stripe border max-height="640" v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="index" width="60" align="center">
			</el-table-column>
      <el-table-column prop="id" label="ID" min-width="40">
			</el-table-column>
      <el-table-column prop="name" label="" min-width="100">
			</el-table-column>
      <el-table-column prop="type" label="" min-width="70">
        <template slot-scope="scope">
          <el-tag v-if="scope.row.type==1" type="1">IOS</el-tag>
          <el-tag v-if="scope.row.type==2" type="2">Android</el-tag>
        </template>
			</el-table-column>
      <el-table-column prop="imageUrl" label="" width="120">
        <template slot-scope="scope">
          <img  :src="scope.row.imageUrl" alt="" style="width: 100px;height: 100px">
        </template>
      </el-table-column>
      <el-table-column prop="versionName" label="" min-width="100">
			</el-table-column>
			<el-table-column prop="versionCode" label="" min-width="60">
			</el-table-column>
      <el-table-column prop="content" label="" min-width="200">
			</el-table-column>
      <el-table-column prop="downloadUrl" label="" width="120">
        <template slot-scope="scope">
          <a  :href="scope.row.downloadUrl"></a>
        </template>
      </el-table-column>
			<el-table-column prop="createTime" label="" min-width="120" :formatter="dateFormat" sortable align="center">
			</el-table-column>
			<el-table-column prop="updateTime" label="" min-width="120" :formatter="dateFormat" sortable align="center">
			</el-table-column>
			<el-table-column label="" width="320" align="center">
				<template slot-scope="scope">
					<el-button type="primary" size="small" @click="$router.push({name:'versionUpdate',params: {id:scope.row.id}})"></el-button>
					<el-button type="danger" size="small" @click="handleDelete(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column>
		</el-table>

		<div class="paginationSection" style="float:right">
			<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageInfo.page.currentPage" :page-sizes="[10, 15, 20, 30, 40]" :page-size="pageInfo.page.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="pageInfo.page.totalNum">
			</el-pagination>
		</div>
		<router-view></router-view>
	</div>
</template>

<style lang="scss" scoped>

</style>

<script>
import axios from "axios";
import api from "../../api";
import util from "../../common/js/util";
import commons from "../../mixins/commons";

export default {
  mixins: [commons],
  data() {
    return {
      //
      pageInfo: {
        tableList: [],
        page: {
          currentPage: 1,
          pageSize: 15,
          totalNum: 0
        }
      },
      listLoading: false,
      sels: [],
      //
      queryParam: storeSession.get("VERSION-QUERY") || {
        query: {
        },
        page: {
          currentPage: 1,
          pageSize: 15,
          totalNum: 0
        }
      }
    };
  },
  created() {
    this.loadPage();
  },
  watch: {
    queryParam: {
      handler(curVal, oldVal) {
        //
        clearTimeout(this.timeoutTask);
        //
        this.timeoutTask = setTimeout(() => {
          this.loadPage();
        }, 400);
      },
      deep: true
    }
  },
  methods: {
    selsChange(sels) {
      this.sels = sels;
    },
    loadPage() {
      storeSession.set("VERSION-QUERY", this.queryParam);
      api.version.list(this.queryParam).then(res => {
        this.pageInfo.tableList = res.data.data.list;
        this.pageInfo.page = res.data.data.page;
      });
    },
    handleSizeChange(val) {
      this.queryParam.page.pageSize = val;
    },
    handleCurrentChange(val) {
      this.queryParam.page.currentPage = val;
    },
    handleDelete(index, row) {
      this.$confirm("", "").then(() => {
        api.version.del(row.id).then(res => {
          this.loadPage();
        });
      });
    },
    reset() {
      this.queryParam = {
        query:{
        },
        page: {
          currentPage: 1,
          pageSize: 15,
          totalNum: 0
        }
      };
    }
  }
};
</script>



