<template>
	<div>
    <div class="queryForm">
			<el-form :inline="true" :model="queryParam" class="demo-form-inline" label-suffix=":" label-width="100px">
				<el-form-item label="">
					<el-select filterable v-model="queryParam.query.type">
						<el-option value="" label="" />
						<el-option v-for="item in typeList" :value="item.code" :label="item.name" :key="item.code" />
					</el-select>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" size="small" @click="loadPage"></el-button>
					<el-button type="primary" size="small" @click="reset"></el-button>
				</el-form-item>
			</el-form>
		</div>
    <el-button type="primary" size="small" @click="$router.push({ name: 'advertisementAdd' })"></el-button>
		<el-table :data="pageInfo.tableList" highlight-current-row stripe border max-height="640" v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="index" width="60" align="center">
			</el-table-column>
      <el-table-column prop="id" label="ID" min-width="40">
			</el-table-column>
      <el-table-column prop="title" label="" min-width="100">
			</el-table-column>
      <el-table-column prop="content" label="" min-width="100">
			</el-table-column>
      <el-table-column prop="type" label="" min-width="70">
        <template slot-scope="scope">
          <el-tag v-if="scope.row.type==1" type="1"></el-tag>
          <el-tag v-if="scope.row.type==2" type="2"></el-tag>
          <el-tag v-if="scope.row.type==3" type="3"></el-tag>
          <el-tag v-if="scope.row.type==6" type="6"></el-tag>
          <el-tag v-if="scope.row.type==4" type="4"></el-tag>
          <el-tag v-if="scope.row.type==5" type="5"></el-tag>
        </template>
			</el-table-column>
      <el-table-column prop="imageUrl" label="" width="120">
        <template slot-scope="scope">
          <img  :src="scope.row.imageUrl" alt="" style="width: 100px;height: 100px">
        </template>
      </el-table-column>
      <el-table-column prop="auditStatus" label="" min-width="70">
        <template slot-scope="scope">
          <el-tag v-if="scope.row.auditStatus==1" auditStatus="1"></el-tag>
          <el-tag v-if="scope.row.auditStatus==2" auditStatus="2"></el-tag>
          <el-tag v-if="scope.row.auditStatus==3" auditStatus="3"></el-tag>
        </template>
			</el-table-column>
      <el-table-column prop="state" label="" min-width="70">
        <template slot-scope="scope">
          <el-tag v-if="scope.row.state==1" state="1"></el-tag>
          <el-tag v-if="scope.row.state==2" state="2"></el-tag>
          <el-tag v-if="scope.row.state==3" state="3"></el-tag>
          <el-tag v-if="scope.row.state==4" state="4"></el-tag>
        </template>
			</el-table-column>
      <el-table-column prop="link" label="" width="120">
        <template slot-scope="scope">
          <a  :href="scope.row.link"></a>
        </template>
      </el-table-column>
			<el-table-column prop="createTime" label="" min-width="120" :formatter="dateFormat" sortable align="center">
			</el-table-column>
			<el-table-column prop="updateTime" label="" min-width="120" :formatter="dateFormat" sortable align="center">
			</el-table-column>
			<el-table-column label="" width="320" align="center">
				<template slot-scope="scope">
					<el-button type="primary" size="small" @click="$router.push({name:'advertisementUpdate',params: {id:scope.row.id}})"></el-button>
					<el-button type="danger" size="small" @click="handleDelete(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column>
		</el-table>

		<div class="paginationSection" style="float:right">
			<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageInfo.page.currentPage" :page-sizes="[10, 15, 20, 30, 40]" :page-size="pageInfo.page.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="pageInfo.page.totalNum">
			</el-pagination>
		</div>
		<router-view></router-view>
	</div>
</template>

<style lang="scss" scoped>

</style>

<script>
import axios from "axios";
import api from "../../api";
import util from "../../common/js/util";
import commons from "../../mixins/commons";

export default {
  mixins: [commons],
  data() {
    return {
      typeList: [{code : 1, name : ""}, {code : 2, name : ""}, {code : 3, name : ""}, {code : 6, name : ""}, {code : 4, name : ""}, {code : 5, name : ""}],
      pageInfo: {
        tableList: [],
        page: {
          currentPage: 1,
          pageSize: 15,
          totalNum: 0
        }
      },
      listLoading: false,
      sels: [],
      //
      queryParam: storeSession.get("ADVERTISEMENT-QUERY") || {
        query: {
        },
        page: {
          currentPage: 1,
          pageSize: 15,
          totalNum: 0
        }
      }
    };
  },
  created() {
    this.loadPage();
  },
  watch: {
    queryParam: {
      handler(curVal, oldVal) {
        //
        clearTimeout(this.timeoutTask);
        //
        this.timeoutTask = setTimeout(() => {
          this.loadPage();
        }, 400);
      },
      deep: true
    }
  },
  methods: {
    selsChange(sels) {
      this.sels = sels;
    },
    loadPage() {
      storeSession.set("ADVERTISEMENT-QUERY", this.queryParam);
      api.advertisement.list(this.queryParam).then(res => {
        this.pageInfo.tableList = res.data.data.list;
        this.pageInfo.page = res.data.data.page;
      });
    },
    handleSizeChange(val) {
      this.queryParam.page.pageSize = val;
    },
    handleCurrentChange(val) {
      this.queryParam.page.currentPage = val;
    },
    handleDelete(index, row) {
      this.$confirm("", "").then(() => {
        api.advertisement.del(row.id).then(res => {
          this.loadPage();
        });
      });
    },
    reset() {
      this.queryParam = {
        query:{
        },
        page: {
          currentPage: 1,
          pageSize: 15,
          totalNum: 0
        }
      };
    }
  }
};
</script>



